body {
  background-color: #f6f6f6;
}
.App {
  max-width: 700px; width: 100%;
  margin: 20px auto;

  box-sizing: border-box;
  padding: 20px;
  border: 1px solid #808080;
  // background-color: antiquewhite;
  display: flex;
  flex-direction: column;
  gap: 30px;
  box-shadow: 5px 5px 10px #ddd;
  border-radius: 10px;
  .Header {
    h3 {
      display: flex; align-items: center;
      &:nth-child(2) {
        font-size: 20px; color : #a61616;
        margin: 20px 0; font-weight: 700;
      }
      &:last-child {
        font-size: 32px; color: #1f93ff;
        margin-top: 20px; font-weight: 700;
      }
    }
  }
  .TodoEditor {
    // background-color: #c6c6c6;
    padding: 10px;
    border: 1px dashed #bbb;
    border-radius: 10px;
    .editor_wrapper {
      width: 100%; display: flex; gap: 10px;
      margin-top: 20px;
      input {
        flex : 1; box-sizing: border-box;
        border: 1px solid rgb(220,220,220);
        border-radius: 5px; padding: 15px;
        &:focus {
          outline: none;
          border: 1px solid #1f93ff;
        }
      }
      button {
        cursor: pointer; width: 80px; border: none;
        background-color: #1f93ff;
        border-radius: 5px;
        color:#fff; font-size: 16px;
      }
    }
  }
  .TodoList {
    // background-color: #a6a6a6;
    padding: 10px;
    border: 1px dashed #bbb;
    border-radius: 10px;
    .totalCount {
      display: flex;
      gap: 30px;
      padding: 20px 0 0 20px;
      p {padding-bottom: 10px;}
    }
    .searchbar {
      margin: 20px 0; width: 100%;
      width: 100%; border: none;
      border-bottom: 1px solid rgb(220,220,220);
      box-sizing: border-box;
      padding: 15px;
      &:focus {
        outline: none; border-color: #1f93ff;
      }
    }
    .list_wrapper {
      display: flex; flex-direction: column; gap: 20px;
      .TodoItem {
        label {
          display: flex; align-items: center; gap: 20px;
          padding-bottom: 20px;
          border-bottom: 1px solid rgb(240,240,240);
        }
        .checkbox_col {
          display: flex;
          width: 20px;
        }
        .title_col {
          flex : 1;
        }
        .title_gray {
          flex : 1;
          color : gray;
          text-decoration : line-through;
        }
        .date_col {
          font-size: 14px; color:gray;
        }
        .btn_col button {
          cursor: pointer; color: gray; font-size: 14px;
          border: none; border-radius: 5px; padding: 5px;
          transition: .4s;
          &:hover {
            background-color: #1f93ff;
            color: #fff;
          }
        }
      }

    }
  }
}
.icon {
  margin: 0 10px;
}
.TestComponent {
  padding: 10px;
  border: 1px dashed #bbb;
  border-radius: 10px;
  // background-color: #ffa1a1;
  display: flex;
  gap: 30px;
}